import { customElement } from 'lit/decorators.js';
import { html, LitElement, TemplateResult } from 'lit';

export type TabData = {
  id: string;
  label: string;
  body: string;
};

@customElement('zwc-tabs-template')
export class ZwcTabsTemplate extends LitElement {
  allText = null;

  render(): TemplateResult {
    return html`<slot @slotchange=${this.handleSlotChange}></slot>`;
  }

  protected handleSlotChange(e: Event) {
    if (e) {
      const childNodes = (e.target as HTMLSlotElement).assignedNodes({
        flatten: true,
      });
      const tabs = childNodes.filter(
        node => node.nodeType !== this.TEXT_NODE,
      ) as HTMLTemplateElement[];
      const data: TabData[] = [];
      tabs.forEach((element: HTMLTemplateElement) => {
        const tab = {} as TabData;
        const children = Array.from(element.content.children);

        const [label] = children.filter(child =>
          child.classList.contains('label'),
        );
        const [body] = children.filter(child =>
          child.classList.contains('body'),
        );

        tab.id = element.getAttribute('tab') as string;
        tab.label = label.innerHTML.trim();
        tab.body = body.innerHTML.trim();

        data.push(tab);
      });

      // Emit collection;
      this.emitTagContent(data);
    }
  }

  emitTagContent(data: TabData[]): void {
    this.dispatchEvent(
      new CustomEvent('change', {
        composed: true,
        cancelable: true,
        bubbles: true,
        detail: data,
      }),
    );
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-tabs-template': ZwcTabsTemplate;
  }
}
